body {
  padding: 1rem;
}

h1, h2, h3 {
  padding: 0;
  margin: 0;
}

h1 {
  text-align: center;
  margin-bottom: 1rem;
  font-size: 1.5rem;
}

h2 {
  font-size: 1.2rem;
}

h3 {
  font-size: 1rem;
}


.totals {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  text-align: center;
  width: 20rem;
  margin: 0 auto;
  overflow: hidden;
  margin-bottom: 1rem;
}

.totals div {
  padding: .5rem;
}

.totals .income {
  background-color: rgb(119, 243, 136);
}

.totals .expenses {
  background-color: rgb(246, 179, 246);
}
.totals .balance {
  color: black;
}

.transaction_list, form {
  list-style: none;
  width: 20rem;
  margin: 0 auto;
}

.transaction_list {
  margin-top: 1rem;
  padding: 0;
}

.transaction_list h3 {
  margin-bottom: .5rem;
}

li {
  display: grid;
  grid-template-columns: min-content 1fr min-content;
  gap: .5rem;
  align-items: center;
  margin-bottom: .5rem;
  padding: .3rem;
  overflow: hidden;
}

.expense-border {
  border-left: 3px solid red;
  background-color: rgb(246, 179, 246);
}

.income-border {
  border-left: 3px solid green;
  background-color: rgb(119, 243, 136);
}

li:hover > button {
  transform: translateX(0);
}

.transaction_item button {
  background-color: red;
  color: white;
  border: none;
  padding: .3rem;
  font-weight: bolder;
  transform: translateX(-150%);
  transition: transform .3s ease;
  cursor: pointer;
}

form {
  background-color: rgb(195, 245, 246);
  padding: 1rem;
  text-align: center;
  border-radius: 1rem;
}

form div {
  display:grid;
  grid-template-columns: 3rem 1fr;
  gap: 1rem;
  justify-content: center;
  align-items: center;
  margin-bottom: 1rem;
  

}

form button {
  background-color: green;
  color: white;
  border: none;
  padding: .5rem;
  cursor: pointer;
}

form input {
  padding: .3rem;
  border: none;
  margin-top: .2rem;
}